.contacts {
  position: relative;


  @media (min-width: $desktop) {
    max-width: 1440px;
    margin: 0 auto;
    padding: 93px 110px 103px 110px;
  }
}


.contacts__wrapper {
  position: relative;
  z-index: 1;

  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 26px 19px;

  background-color: $color-white;

  &--grey {
    background-color: $color-white-smoke;
  }

  @media (min-width: $tablet) {
    align-items: flex-start;
    padding: 63px 72px 57px 71px;
  }

  @media (min-width: $desktop) {
    top: 0;
    z-index: 1;

    width: 100%;
    max-width: 570px;
    padding: 66px 82px 58px 76px;

    background: $color-white;
  }
}

.contacts__title {
  max-width: 140px;
  margin: 0;

  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  font-family: $font-family--second;
  color: $color-black-second;
  text-transform: uppercase;

  @media (min-width: $tablet) {
    font-size: 20px;
    line-height: 25px;
  }
}

.contacts__text {
  max-width: 140px;

  font-style: normal;
  font-size: 14px;
  line-height: 20px;
  font-family: $font-family--main;
  text-align: left;
  color: $color-charcoal;

  @media (min-width: $tablet) {
    max-width: 160px;

    font-size: 16px;
    line-height: 20px;
    text-align: right;


    @media (min-width: $desktop) {
      text-align: left;
    }
  }
}

.contacts__text-city {
  @media (min-width: $tablet) {
    display: block;
    margin-top: 19px;

    text-align: right;
  }

  @media (min-width: $desktop) {
    text-align: left;
  }
}

.contacts__map {
  width: 100%;
  height: 362px;

  background-image: url("../img/background/location.jpg");
  background-repeat: round;

  @media (min-width: $tablet) {
    height: 400px;

    background-image: url("../img/background/location-tablet.jpg");
  }

  @media (min-width: $desktop) {
    position: absolute;
    top: 0;
    left: 50%;

    width: 100vw;
    height: 100%;
    overflow: hidden;

    background-image: url("../img/background/location-desktop.jpg");
    transform: translateX(-50%);
  }
}
